.nav{
    z-index: 9999;
  }
  
  .nav.scrolled{
    background:#222222 !important;
  }

  .nav-menu{
    color:white;
    font-size:16px;
    position: relative;
    display: block;
    text-decoration: none;
    transition: 0.5s;
}

.nav-menu:hover{
    color:white;
    text-decoration: none;
}

.nav-menu::after{
    position: absolute;
    content: "";
    width: 100%;
    height: 0.1rem;
    top: 100%;
    left: 0;
    background: #ffffff;
    transition: transform 0.5s;
    transform: scaleX(0);
    transform-origin: right;
}

.nav-menu:hover::after{
    transform: scaleX(1);
    transform-origin: left;
}

  .activated-nav{
    width: 100%;
    height: 0.1rem;
}